body{
    font-family: "Neue Haas Unica";
    background-color: black;
    width: 100%;
}
html, body, #root, .container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.navigation{
    position: absolute;
    text-align: center;
    top: 10px;
    margin-top: 2vh;
    text-decoration: none;
    color: white;
}

.aboutContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50vw;
    height: 45vh;
    background-color: white;
}
h3, h5, p{
    margin-top: 0;
    margin-bottom: 0;
}